interface IDOType {
	lockPeriod: number;
	apy: number;
	extendsLockOnRegistration: boolean;
	earlyUnstakeFee: number;
	unstakeFee: number;
	locked: boolean;
}
interface StakingStatus {
	ido: IDOType;
	balance: number;
	stakedAmount: number;
	BNBTotalAmountToWallet: number;
	claimStatus: boolean;
}
interface investInfo {
	startDate: string;
	startTime: string;
	investBNB: number;
	maxBNB: number;
	paidBNB: number;
	available: number;
}
