import { ethers } from "ethers";
import contracts from "./contracts.json";
import abiBNB from "./abi/BNBtoken.json";
import abiStaking from "./abi/staking.json";
import abiStaking1 from "./abi/staking1.json";

const rpc = process.env.REACT_APP_NETWORK_URL;
const chainid = process.env.REACT_APP_CHAIN_ID;
const addrs = contracts[chainid];

const provider = new ethers.providers.JsonRpcProvider(rpc);

const BNBStakingContract = new ethers.Contract(
  addrs.staking.BNB1,
  abiStaking,
  provider
);
console.log(addrs.staking.BNB1);
console.log(provider);

const BNBStakingContract1 = new ethers.Contract(
  addrs.staking.BNB1,
  abiStaking1,
  provider
);


export { provider, BNBStakingContract, BNBStakingContract1, contracts };
