import React, { useEffect, useState } from "react";
import { Link, Outlet } from "react-router-dom";
// import WalletConnect from "@walletconnect/client";//add
// import WalletConnectQRCodeModal from "@walletconnect/qrcode-modal";//add
import {
  errHandler,
  tips,
  /* toValue, */ fromValue,
  fromBigNum,
  walletconnect,
} from "./util";
import { useWeb3React, UnsupportedChainIdError } from "@web3-react/core";
import { useWallet } from "./hooks/useWallet";

import "./layout.scss";
import "./assets/css/stars.css";
import Meta from "./assets/img/metamask.png";
import Trust from "./assets/img/trust.png";
import Coinbase from "./assets/img/coinbase.png";
import { useWebContext } from "./context";

const Layout = (props: any) => {
  const { active, connect, chainId } = useWallet();
  const { account } = useWeb3React();
  const [scrolling, setScrolling] = React.useState<boolean>(false);
  const [up, setUp] = React.useState<boolean>(false);
  const { activate, connector } = useWeb3React();
  const [state, { dispatch }] = useWebContext();

  useEffect(() => {
    window.addEventListener("scroll", onScroll, true);
  }, []);
  let scrollTop = 0;
  const onScroll = () => {
    const scroll = document.body.scrollTop;
    if (scroll > 80) {
      if (scroll > scrollTop) {
        setScrolling(true);
        setUp(false);
      } else {
        setUp(true);
      }
    } else {
      setScrolling(false);
      setUp(false);
    }
    scrollTop = scroll;
  };

  const [WalletConnectModal, setWalletConnectModal] = useState(false);

  const handleConnect = async (key: string) => {
    try {
      await connect(key);
      setWalletConnectModal(!WalletConnectModal);
      if (account !== undefined) {
        dispatch({
          type: "disconnect_able",
          payload: true,
        });
      } else {
        dispatch({
          type: "disconnect_able",
          payload: false,
        });
      }
      //wallet modal cancel
    } catch (err) {
      console.log({ err });
    }
  };
  const WalletDisconectCall = () => {
    dispatch({
      type: "disconnect_able",
      payload: false,
    });
  };

  const WalletModal = () => {
    return (
      <div className="modal-continer">
        <div
          className="modal-back"
          onClick={() => setWalletConnectModal(false)}
        ></div>

        <div className="modal-body wallet-modal">
          <div className="justify">
            <div className="wallet-icon-hover">
              <a onClick={() => handleConnect("injected")}>
                <img
                  src={Meta}
                  className="justify wallet-imgs"
                  alt="metamask"
                />
              </a>
            </div>
            <div className="wallet-icon-hover">
              <a onClick={() => handleConnect("walletconnect")}>
                <img src={Trust} className="justify wallet-imgs" alt="Trust" />
              </a>
            </div>
            
          </div>
        </div>
      </div>
    );
  };
  useEffect(() => {
    if (account !== undefined) {
      dispatch({
        type: "disconnect_able",
        payload: true,
      });
    } else {
      dispatch({
        type: "disconnect_able",
        payload: false,
      });
    }
    console.log(chainId);
  }, [account, chainId]);

  return (
    <>
      <header>
        <div className="container-fluid">
          <nav className="navbar navbar-expand-md navbar-dark">
            <Link className="navbar-brand heading-black" to="./">
              <img
                src="images/43F49EB6-794E-42A5-9E65-AB6AC1156E5C.svg"
                loading="lazy"
                id="w-node-_33e865e5-d2e1-eadf-e318-5c96cb555b3f-5e8054b8"
                alt=""
                className="image-5"
              />
            </Link>
            <div className="navbar-collapse collapse dis-f">
              <ul className="navbar-nav ml-auto flex-align-center align-items-center">
                <li className="nav-item">
                  <a className="nav-link page-scroll p-0">
                    {state.disconnect_able ? (
                      <button
                        onClick={() => {
                          WalletDisconectCall();
                        }}
                        className="btn btn-primary d-inline-flex flex-row align-items-center"
                        id="loginButton"
                      >
                        disconnect
                      </button>
                    ) : (
                      <button
                        onClick={() => setWalletConnectModal(true)}
                        className="btn btn-primary d-inline-flex flex-row align-items-center"
                        id="loginButton"
                      >
                        Connect
                      </button>
                    )}
                  </a>
                </li>
                <li className="nav-item">
                  <a className="nav-link page-scroll p-0">
                    {state.disconnect_able &&
                      account &&
                      account.length !== 0 &&
                      account.slice(0, 4) +
                        "..." +
                        account.slice(account.length - 4, account.length)}
                  </a>
                </li>
              </ul>
            </div>
          </nav>
        </div>
      </header>
      <main>{props.children}</main>

      {WalletConnectModal === true ? <WalletModal /> : <></>}

      <Outlet />
    </>
  );
};

export default Layout;
